@use '/src/styles/constants/colors';
@use '/src//styles/mixins/position';

.toggleContainer {
  margin-top: 50px;

  h1 {
    display: block;
    color: colors.$BIG_TITLE;
    font-weight: 600;
    font-size: 32px;
  }
}

.toggleWarpper {
  position: relative;
  display: flex;
  align-items: center;
  width: 300px;
  height: 50px;
  margin-top: 10px;
  color: colors.$GRAYD;
  background: colors.$WHITE;
  border: 3px solid colors.$GRAYD;
  border-radius: 25px;
  transition: 0.8s ease-in-out;

  .underButton {
    z-index: 3;
    width: 50%;
    height: 99%;
    text-align: center;
    transition: 0.9s ease-out;
  }

  .toggleButton {
    @include position.absolute(0);
    width: 50%;
    height: 99%;
    font-weight: 700;
    background: colors.$GRAYE;
    border-radius: 25px;
    transition: 0.9s ease-in-out;

    &:hover {
      background: colors.$TASKS_TITLE;
    }
  }
}
